<template lang="html">
<section id="ajax-page" class="pt-page ajax-page-content">
    <div class="ajax-page-wrapper">
        <div class="ajax-page-nav">
            <div class="nav-item ajax-page-prev-next">
                <router-link v-if="prev_id" :to="{ name: 'portfoliodetail', params: { id: prev_id }}" class="ajax-page-load"><i class="pe-7s-icon pe-7s-angle-left"></i></router-link>
                <router-link v-if="next_id" :to="{ name: 'portfoliodetail', params: { id: next_id }}" class="ajax-page-load"><i class="pe-7s-icon pe-7s-angle-right"></i></router-link>
            </div>
            <div class="nav-item ajax-page-close-button">
                <router-link :to="{ name: 'portfolio'}"><i class="pe-7s-icon pe-7s-close"></i></router-link>
            </div>
        </div>

        <div class="ajax-page-title">
            <h1>{{ portfolio.title }}</h1>
        </div>

        <div class="row">
            <div class="col-sm-7 col-md-7 portfolio-block" v-show="portfolio.galleries || portfolio.videos || portfolio.images">
                <div class="owl-carousel portfolio-page-carousel" v-show="portfolio.galleries">
                    <div class="item" v-for="g in portfolio.galleries">
                        <img :src="g" alt="">
                    </div>
                </div>

                <template v-for="v in portfolio.videos">
                <div class="portfolio-page-video embed-responsive embed-responsive-16by9">
                  <iframe class="embed-responsive-item" :src="v"></iframe>
                </div>
                </template>

                <template v-for="i in portfolio.images">
                <div class="portfolio-page-image">
                    <img :src="i" alt="">
                </div>
                </template>
            </div>

            <div class="col-sm-5 col-md-5 portfolio-block">
                <!-- Project Description -->
                <div class="block-title" v-show="portfolio.description_name">
                    <h3>{{ portfolio.description_name }}</h3>
                </div>
                <ul class="project-general-info">
                    <li v-show="portfolio.author"><p><i class="fa fa-user"></i> {{ portfolio.author }}</p></li>
                    <li v-show="portfolio.website"><p><i class="fa fa-globe"></i> <a :href="portfolio.website" target="_blank">{{ portfolio.website }}</a></p></li>
                    <li v-show="portfolio.date"><p><i class="fa fa-calendar"></i> {{ portfolio.date }}</p></li>
                </ul>

                <div v-html="portfolio.content"></div>
                <!-- /Project Description -->

                <!-- Technology -->
                <div class="tags-block">
                    <div class="block-title" v-show="portfolio.tag_name">
                        <h3>{{ portfolio.tag_name }}</h3>
                    </div>
                    <ul class="tags" v-show="portfolio.tags">
                        <li v-for="t in portfolio.tags"><a :href="t.url">{{ t.name }}</a></li>
                    </ul>
                </div>
                <!-- /Technology -->

                <!-- Share Buttons -->
                <div class="btn-group share-buttons">
                    <div class="block-title" v-show="portfolio.share_name">
                        <h3>{{ portfolio.share_name }}</h3>
                    </div>
                    <a v-for="s in portfolio.shares" :href="s.url" target="_blank" class="btn"><i :class="'fa '+s.icon"></i> </a>
                </div>
                <!-- /Share Buttons -->
            </div>
        </div>
    </div>
</section>

</template>
