      };
      
      // this.blogs.forEach(function(item,index){
      //   if(item.id == that.$route.params.id){
      //     that.post = item
      //   }
      // })
    },
    mounted(){
        jQuery(document).ready(function($){
            $('.portfolio-page-carousel').owlCarousel({
                smartSpeed:1200,
                items: 1,
                loop: true,
                dots: true,
                nav: true,
                navText: false,
                margin: 10
            });
        });
    }
}
</script>


