<template lang="html">
<!-- Portfolio Subpage -->
<section class="pt-page pt-page-4" data-id="portfolio">
  <div class="border-block-top-110"></div>
  <div class="section-inner">
    <div class="section-title-block">
      <div class="section-title-wrapper">
        <h2 class="section-title">{{ name }}</h2>
        <h5 class="section-description">{{ description }}</h5>
      </div>
    </div>

    <!-- Portfolio Content -->
    <div class="portfolio-content">
                  
      <!-- Portfolio filter -->
      <ul id="portfolio_filters" class="portfolio-filters">
        <li v-bind:class="i == 0 ? 'active':''" v-for="(f, i) in filters">
          <a v-bind:class="i == 0 ? 'active':''" class="filter btn btn-sm btn-link active" :data-group="f.group">{{ f.name }}</a>
        </li>
      </ul>
      <!-- End of Portfolio filter -->

      <!-- Portfolio Grid -->
      <div id="portfolio_grid" class="portfolio-grid portfolio-masonry masonry-grid-3">

        <!-- Portfolio Item 1 -->
        <figure class="item" :data-groups="groups_data(p.groups)" v-for="p in portfolios">
            <router-link v-if="p.type == 'page'" :to="{ name: 'portfoliodetail', params: { id: p.id } }" :title="p.title" :class="p.class">
                <img :src="p.thumb" :alt="p.title">
                <div>
                    <h5 class="name">{{ p.name }}</h5>
                    <small>{{ p.group_name }}</small>
                    <i :class="'pe-7s-icon '+p.icon"></i>
                </div>
            </router-link>
            <a v-if="p.type != 'page'" :href="p.url" :title="p.title" :class="p.class">
                <img :src="p.thumb" :alt="p.title">
                <div>
                    <h5 class="name">{{ p.name }}</h5>
                    <small>{{ p.group_name }}</small>
                    <i :class="'pe-7s-icon '+p.icon"></i>
                </div>
            </a>
        </figure>
        <!-- /Portfolio Item 1 -->

      </div>
      <!-- /Portfolio Grid -->

    </div>
    <!-- /Portfolio Content -->
  </div>
</section>
<!-- /Portfolio Subpage -->
</template>
<script>
export default {
    data () {
        return {
            name:'Portfolio',
            description:'My Best Works',
            filters:[
                {
                    name:'All',
                    group:'all'
                },{
                    name:'Media',
                    group:'media'
                },{
                    name:'Illustration',
                    group:'illustration'
                },{
                    name:'Video',
                    group:'video'
                }
            ],
            portfolios:[]
        }
    },
    created(){
        this.addPortfolio({
            id:1,
            groups:['all','media'],
            name:'Project Name',
            group_name:'Media',
            title:'',
            url:'#',
            thumb:'/assets/images/portfolio/1.jpg'
        },'page');
        this.addPortfolio({
            id:2,
            groups:['all','video'],
            name:'Project Name',
            group_name:'Video',
            title:'Praesent Dolor Ex',
            url:'https://player.vimeo.com/video/97102654?autoplay=1',
            thumb:'/assets/images/portfolio/2.jpg'
        },'video');
        this.addPortfolio({
            id:3,
            groups:['all','illustration'],
            name:'Project Name',
            group_name:'Illustration',
            title:'Duis Eu Eros Viverra',
            url:'/assets/images/portfolio/full/3.jpg',
            thumb:'/assets/images/portfolio/3.jpg'
        },'image');
        this.addPortfolio({
            id:4,
            groups:['all','media'],
            name:'Project Name',
            group_name:'Media',
            title:'',
            url:'#',
            thumb:'/assets/images/portfolio/4.jpg'
        },'page');
        this.addPortfolio({
            id:5,
            groups:['all','illustration'],
            name:'Project Name',
            group_name:'Illustration',
            title:'Aliquam Condimentum Magna Rhoncus',
            url:'/assets/images/portfolio/full/5.jpg',
            thumb:'/assets/images/portfolio/5.jpg'
        },'image');
        this.addPortfolio({
            id:6,
            groups:['all','media'],
            name:'Project Name',
            group_name:'Media',
            title:'',
            url:'#',
            thumb:'/assets/images/portfolio/6.jpg'
        },'page');
        this.addPortfolio({
            id:7,
            groups:['all','video'],
            name:'Project Name',
            group_name:'Video',
            title:'Nulla Facilisi',
            url:'https://player.vimeo.com/video/97102654?autoplay=1',
            thumb:'/assets/images/portfolio/7.jpg'
        },'video');
        this.addPortfolio({
            id:8,
            groups:['all','media'],
            name:'Project Name',
            group_name:'Media',
            title:'',
            url:'/assets/images/portfolio/8.jpg',
            thumb:'/assets/images/portfolio/8.jpg'
        },'image');
        this.addPortfolio({
            id:9,
            groups:['all','illustration'],
            name:'Project Name',
            group_name:'Illustration',
            title:'Mauris Neque Dolor',
            url:'/assets/images/portfolio/full/9.jpg',
            thumb:'/assets/images/portfolio/9.jpg'
        },'image');
        this.addPortfolio({
            id:10,
            groups:['all','video'],
            name:'Project Name',
            group_name:'Video',
            title:'Donec Lectus Arcu',
            url:'https://player.vimeo.com/video/97102654?autoplay=1',
            thumb:'/assets/images/portfolio/10.jpg'
        },'video');
        this.addPortfolio({
            id:11,
            groups:['all','illustration'],
            name:'Project Name',
            group_name:'Illustration',
            title:'Duis Eu Eros Viverra',
            url:'/assets/images/portfolio/full/11.jpg',
            thumb:'/assets/images/portfolio/11.jpg'
        },'image');
        this.addPortfolio({
            id:12,
            groups:['all','media'],
            name:'Project Name',
            group_name:'Media',
            title:'Duis Eu Eros Viverra',
            url:'#',
            thumb:'/assets/images/portfolio/12.jpg'
        },'page');
    },
    methods: {
        addPortfolio(obj, type) {
            if(type == 'page'){
                obj.class = 'ajax-page-load';
                obj.icon = 'pe-7s-display2';
                obj.type = 'page';
            }
            if(type == 'video'){
                obj.class = 'lightbox mfp-iframe';
                obj.icon = 'pe-7s-video';
                obj.type = 'video';
            }
            if(type == 'image'){
                obj.class = 'lightbox';
                obj.icon = 'pe-7s-photo';
                obj.type = 'image';
            }
            this.portfolios.push(obj);
        },
        groups_data(groups){
            var html = '[';
            groups.forEach(function(e,i){
                if(i>0){
                    html += ','
                }
                html += '"'+e+'"'
            })
            html += ']'
            return html
        }
    }
}
</script>



