<template>
<!-- Header -->
<header id="site_header" class="header mobile-menu-hide header-color-light">
  <div class="my-photo tilt-effect">
    <img src="/assets/images/photo.png" alt="image">
  </div>

  <div class="site-title-block">
    <h1 class="site-title">{{ name }}</h1>
  </div>

  <!-- Navigation -->
  <div class="site-nav">
    <!-- Main menu -->
    <ul id="nav" class="site-main-menu">
        <router-link v-for="post in posts" v-bind:key="post.to" tag="li" active-class="active" exact class="pt-trigger" :to="{ name: post.to }" :data-id="post.to">
            <a :data-animation="post.animation" :data-id="post.to">
                <i :class="'menu-icon pe-7s-icon pe-7s-'+post.icon"></i> {{post.name}}
            </a>
        </router-link>
    </ul>
    <!-- /Main menu -->
  </div>
  <!-- Navigation -->
</header>
<!-- /Header -->
</template>

<script>
export default {
    data () {
        return {
            name: 'Phú',
            posts: [
              {
                'animation':21,
                'icon':'home',
                'name':'Home',
                'to':'home',
              },
              {
                'animation':17,
                'icon':'user',
                'name':'About me',
                'to':'about',
              },
              {
                'animation':18,
                'icon':'id',
                'name':'Resume',
                'to':'resume',
              },
              {
                'animation':19,
                'icon':'news-paper',
                'name':'Blog',
                'to':'blog',
              },
              {
                'animation':19,
                'icon':'portfolio',
                'name':'Portfolio',
                'to':'portfolio',
              },
              {
                'animation':20,
                'icon':'mail',
                'name':'Contact',
                'to':'contact',
              },
            ],
        }
    },
}
</script>



