<template lang="html">
<nav>
    <ul class="pagination">
        <li v-if="pagination.current > 1">
            <router-link class="tip social-button" :to="{ name: 'blogpagination', params: { paged: pagination.current - 1 } }"><</router-link>
        </li>
        <li v-if="pagiIsFirst()">
            <router-link class="tip social-button" :to="{ name: 'blogpagination', params: { paged: 1 } }">1</router-link>
        </li>
        <li v-if="pagination.current > range_num * 2">
            <span class="tip social-button">&hellip;</span>
        </li>
        <template v-for="i in range()">
            <li v-if="i == pagination.current" v-bind:class="i == pagination.current ? 'active' : ''">
                <span class="tip social-button">{{ i }}</span>
            </li>
            <li v-if="i != pagination.current" v-bind:class="i == pagination.current ? 'active' : ''">
                <router-link class="tip social-button" :to="{ name: 'blogpagination', params: { paged: i } }">{{ i }}</router-link>
            </li>
        </template>
        <li v-if="pagination.current < pagination.pages - range_num - 1">
            <span class="tip social-button">&hellip;</span>
        </li>
        <li v-if="pagiIsLast()">
            <router-link class="tip social-button" :to="{ name: 'blogpagination', params: { paged: pagination.pages } }">{{ pagination.pages }}</router-link>
        </li>
        <li v-if="pagination.current <= pagination.pages - 1">
            <router-link class="tip social-button" :to="{ name: 'blogpagination', params: { paged: pagination.current + 1 } }">></router-link>
        </li>
    </ul>
</nav>
</template>

<script>
export default {
    props: {
        pagination : {
            type: Object,
            default: function () {
                return {
                    current:1,
                    pages:20,
                    per_page:10,
                }
            }
        }
    },
    data(){
        return {
            range_num:2
        }
    },
    methods:{
        pagiIsFirst(){
            if(this.pagination.current <= this.range_num + 1){
                return false
            }
            return true
        },
        pagiIsLast(){
            if(this.pagination.current >= this.pagination.pages - this.range_num){
                return false
            }
            return true
        },
        range(){
            var range          = this.range_num;
            var from           = 0
            var to             = 0
            var current        = this.pagination.current
            var posts_per_page = this.pagination.per_page
            var total_pages    = this.pagination.pages

            from = current - range
            if(from < 1){
                from = 1
            }
            to = current + range;
            if(to > total_pages){
                to = total_pages
            }

            var array = [],
                j = 0;
            for(var i = from; i <= to; i++){
                array[j] = i;
                j++;
            }
            return array
        },
    }
}
</script>

<style lang="scss" scoped>
ul.pagination {
    display: block;
    list-style: none;
    margin: 25px 0 5px;
    padding: 0;
    text-align: left;
    li {
        display: inline-block;
        margin: 0;
        padding: 0;
        span,
        a {
            display: inline-block;
            margin: 0 1px;
            width: 34px;
            height: 34px;
            color: #555;
            padding: 6px 0;
            background-color: transparent;
            border: 1px solid #e5e5e5;
            text-align: center;
            &:hover {
                color: #fff;
                background-color: #ff724c;
                border-color: transparent;
            }
            i {
                font-size: 16px;
                line-height: 32px;
            }
        }
        &.active{
            span,
            a {
                color: #fff;
                background-color: #ff724c;
                border-color: transparent;
            }
        }
        &:last-child a {
            margin-right: 0;
        }
    }
}
</style>



