<template lang="html">
<!-- Resume Subpage -->
<section class="pt-page pt-page-3 stop-skill" data-id="resume">
  <div class="border-block-top-110"></div>
  <div class="section-inner">
    <div class="section-title-block">
      <div class="section-title-wrapper">
        <h2 class="section-title">{{ name }}</h2>
        <h5 class="section-description">{{ description }}</h5>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-6 col-md-4 subpage-block">
        <template v-for="col in colOne">
            <div class="block-title">
              <h3>{{ col.name }}</h3>
            </div>
            <div class="timeline">
              <!-- Single event -->
              <div class="timeline-event te-primary" v-for="item in col.items">
                <h5 class="event-date">{{ item.date }}</h5>
                <h4 class="event-name">{{ item.name }}</h4>
                <span class="event-description">{{ item.description }}</span>
                <p v-show="item.content">{{ item.content }}</p>
              </div>
            </div>
        </template>
      </div>

      <div class="col-sm-6 col-md-4 subpage-block">
        <template v-for="col in colTwo">
            <div class="block-title">
              <h3>{{ col.name }}</h3>
            </div>
            <div class="timeline">
              <!-- Single event -->
              <div class="timeline-event te-primary" v-for="item in col.items">
                <h5 class="event-date">{{ item.date }}</h5>
                <h4 class="event-name">{{ item.name }}</h4>
                <span class="event-description">{{ item.description }}</span>
                <p v-show="item.content">{{ item.content }}</p>
              </div>
            </div>
        </template>
      </div>

      <div class="col-sm-6 col-md-4 subpage-block">
        <template v-for="col in colThree">
            <div class="block-title">
                <h3>{{ col.name }}</h3>
            </div>
            <div class="skills-info">
                <template v-for="item in col.items">
                    <h4>{{ item.name }}</h4>
                    <div class="skill-container">
                        <div :class="'skill-percentage skill-'+item.percentage"></div>
                    </div>
                </template>
            </div>
        </template>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12 col-md-12">
        <div class="download-cv-block">
          <a class="button" target="_blank" :href="downloadUrl">{{ downloadLabel }}</a>
        </div>
      </div>
    </div>
  </div>
</section>
<!-- End Resume Subpage -->
</template>

<script>
export default {
    data () {
        return {
            name: 'Resume',
            description: '5 Years of Experience',
            downloadUrl:'#',
            downloadLabel:'Download CV',
            colOne: [
                {
                    'name':'Education',
                    'items':[
                        {
                            'date':'Sep 2010 - Aug 2013',
                            'name':'Network administrator',
                            'description':'An Giang Vocational College',
                            // 'content':'Mauris magna sapien, pharetra consectetur fringilla vitae, interdum sed tortor.'
                        }
                    ],
                },
            ],
            colTwo: [
                {
                    'name':'Experience',
                    'items':[
                        {
                            'date':'Jun 2016 - Current',
                            'name':'Developer',
                            'description':'Cybridge Asia',
                            // 'content':'Mauris magna sapien, pharetra consectetur fringilla vitae, interdum sed tortor.'
                        },
                        {
                            'date':'Jan 2015 - Jun 2016',
                            'name':'Developer',
                            'description':'EPLUS Ltd., Co.',
                            // 'content':'Mauris magna sapien, pharetra consectetur fringilla vitae, interdum sed tortor.'
                        },
                        {
                            'date':'Nov 2013 - Jan 2015',
                            'name':'Frontend developer',
                            'description':'Minh Thuan Advertising Company',
                            // 'content':'Mauris magna sapien, pharetra consectetur fringilla vitae, interdum sed tortor.'
                        }
                    ],
                },
            ],
            colThree:[
                {
                    'name':'Coding Skills',
                    'items':[
                        {
                            'name':'HML5',
                            'percentage':4
                        },{
                            'name':'CSS3',
                            'percentage':5
                        },{
                            'name':'SCSS',
                            'percentage':5
                        },{
                            'name':'jQuery',
                            'percentage':6
                        },{
                            'name':'Vuejs',
                            'percentage':6
                        }
                    ]
                },{
                    'name':'Backend Skills',
                    'items':[
                        {
                            'name':'Wordpress',
                            'percentage':7
                        },{
                            'name':'Laravel',
                            'percentage':7
                        },{
                            'name':'PHP',
                            'percentage':8
                        }
                    ]
                },{
                    'name':'Server Skills',
                    'items':[
                        {
                            'name':'Vagrant',
                            'percentage':1
                        },{
                            'name':'CentOS',
                            'percentage':2
                        }
                    ]
                }
            ]
        }
    },
}
</script>



