<template lang="html">
  <!-- Home Subpage -->
  <section class="pt-page pt-page-1 section-without-bg section-paddings-0 table" data-id="home">
    <div class="section-inner">
      <div class="home-page-block">
        <h2>{{ name }}</h2>
        <div class="owl-carousel text-rotation">
          <div class="item" v-for="slider in sliders">
            <p class="home-page-description">{{ slider.name }}</p>
          </div>
        </div>
      </div>
    </div>
  </section>
  <!-- End of Home Subpage -->
</template>

<script>
export default {
    data () {
        return {
            name: 'Quang Ngọc Phú',
            sliders:[
                {
                    name:'Full Stack Developer'
                },{
                    name:'Pianist'
                }
            ]
        }
    },
}
</script>



