<template>
<div id="page" class="page">

    <top-header></top-header>

    <top-header-mobile></top-header-mobile>

    <!-- Main Content -->
    <div id="main" class="site-main">
        <!-- Page changer wrapper -->
        <div class="pt-wrapper" style="background-image: url(/assets/images/bg.png);">
            <!-- Subpages -->
            <div class="subpages">
                <router-view></router-view>
            </div>
        </div>
        <!-- /Page changer wrapper -->
    </div>
    <!-- /Main Content -->
</div><!-- /Page -->
</template>

<script>
import axios from 'axios'
import TopHeader from './components/Header.vue'
import TopHeaderMobile from './components/HeaderMobile.vue'

export default {
    name: 'app',
    components: {
        TopHeader,
        TopHeaderMobile,
    },
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            posts: [],
            endpoint: 'https://jsonplaceholder.typicode.com/posts/',
        }
    },
    created() {
        this.getAllPosts();
    },
    methods: {
        getAllPosts() {
            axios.get(this.endpoint)
                .then(response => {
                    this.posts = response.data;
                })
                .catch(error => {
                    console.log('-----error-------');
                    console.log(error);
                })
        }
    }
}
</script>



