import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'
import App from './App.vue'
import Home from './pages/Home.vue'
import About from './pages/About.vue'
import Resume from './pages/Resume.vue'
import Blog from './pages/Blog.vue'
import BlogDetail from './pages/BlogDetail.vue'
import Portfolio from './pages/Portfolio.vue'
import PortfolioDetail from './pages/PortfolioDetail.vue'
import Contact from './pages/Contact.vue'

Vue.use(Router)
Vue.use(Meta)

const router = new Router({
    routes: [
        {
            path: '/',
            name:'home',
            component: Home,
        },
        {
            path: '/about',
            name:'about',
            component: About,
        },
        {
            path: '/resume',
            name:'resume',
            component: Resume,
        },
        {
            path: '/blog',
            name:'blog',
            component: Blog,
        },
        {
            path: '/blog/paged/:paged',
            name:'blogpagination',
            component: Blog,
            props: true,
        },
        {
            path: '/blog/:id',
            name:'blogdetail',
            component: BlogDetail,
            props: true,
        },
        {
            path: '/portfolio',
            name:'portfolio',
            component: Portfolio,
        },
        {
            path: '/portfolio/:id',
            name:'portfoliodetail',
            component: PortfolioDetail,
            props: true,
        },
        {
            path: '/contact',
            name:'contact',
            component: Contact,
        },
    ],
    // mode: 'history'
})

const app = new Vue({
    el: '#app',
    data: { loading: false },
    render: h => h(App),
    router
})
if(router.history.ready){
    $('.pt-page').addClass('pt-page-current')
    if(router.history.current.name == 'resume'){
        setTimeout(function(){
            $('.pt-page').removeClass('stop-skill')
        },500)
    }
}
router.beforeEach((to, from, next) => {
    app.loading = true;
    // $('.preloader').show()
    if(from.name == 'home'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page home
    if(from.name == 'about'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page about
    if(from.name == 'resume'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page resume
    if(from.name == 'blog'){
        if(to.name == 'blogpagination'){
            $('.pt-page').addClass('pt-page-scaleDown')
        }else{
            $('.pt-page').removeClass('pt-page-moveFromBottom')
            $('.pt-page').addClass('pt-page-moveToTop')
        }
    } // From Page blog
    if(from.name == 'blogdetail'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page blogdetail
    if(from.name == 'portfolio'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page portfolio
    if(from.name == 'portfoliodetail'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page portfoliodetail
    if(from.name == 'contact'){
        $('.pt-page').addClass('pt-page-scaleDown')
    } // From Page contact
    setTimeout(() => {
        next()
    }, 500);
})

router.afterEach((to, from) => {
    setTimeout(() => app.loading = false, 500);
    $( document ).ready(function() {
        setTimeout(() => {
            // $('.preloader').fadeOut("slow");
            if(to.name == 'home'){
                $('.pt-page').addClass('pt-page-current pt-page-scaleUpDown')
                // Text rotation
                $('.text-rotation').owlCarousel({
                    loop: true,
                    dots: false,
                    nav: false,
                    margin: 10,
                    items: 1,
                    autoplay: true,
                    autoplayHoverPause: false,
                    autoplayTimeout: 3800,
                    animateOut: 'zoomOut',
                    animateIn: 'zoomIn'
                });
            } // To Page home
            if(to.name == 'about'){
                $('.pt-page').addClass('pt-page-current pt-page-moveFromRight pt-page-ontop')
                // Testimonials Slider
                $(".testimonials.owl-carousel").owlCarousel({
                    nav: true, // Show next/prev buttons.
                    items: 3, // The number of items you want to see on the screen.
                    loop: false, // Infinity loop. Duplicate last and first items to get loop illusion.
                    navText: false,
                    margin: 10,
                    responsive : {
                        // breakpoint from 0 up
                        0 : {
                            items: 1,
                        },
                        // breakpoint from 480 up
                        480 : {
                            items: 1,
                        },
                        // breakpoint from 768 up
                        768 : {
                            items: 2,
                        },
                        1200 : {
                            items: 3,
                        }
                    }
                });
            } // To Page about
            if(to.name == 'resume'){
                $('.pt-page').addClass('pt-page-current pt-page-moveFromLeft pt-page-ontop')
                setTimeout(function(){
                    $('.pt-page').removeClass('stop-skill')
                },500)
            } // To Page resume
            if(to.name == 'blog'){
                $('.pt-page').addClass('pt-page-current pt-page-moveFromBottom')
            } // To Page blog
            if(to.name == 'blogpagination'){
                $('.pt-page').removeClass('pt-page-scaleDown')
                $('.pt-page').addClass('pt-page-current pt-page-moveFromBottom')
            }
            if(to.name == 'blogdetail'){
                $('.pt-page').addClass('pt-page-current pt-page-moveFromBottom')
            } // To Page blogdetail
            if(to.name == 'portfolio'){
                $('.pt-page').addClass('pt-page-current pt-page-moveFromTop')
                // Initialize Portfolio grid
                var $portfolio_container = $("#portfolio_grid");

                $portfolio_container.imagesLoaded(function () {
                    setTimeout(function(){
                        // Portfolio subpage filters
                        var portfolio_grid = $('#portfolio_grid'),
                            portfolio_filter = $('#portfolio_filters');
                            
                        if (portfolio_grid) {

                            portfolio_grid.shuffle({
                                speed: 450,
                                itemSelector: 'figure'
                            });

                            $('.site-main-menu').on("click", "a", function (e) {
                                portfolio_grid.shuffle('update');
                            });

                            portfolio_filter.on("click", ".filter", function (e) {
                                portfolio_grid.shuffle('update');
                                e.preventDefault();
                                $('#portfolio_filters .filter').parent().removeClass('active');
                                $(this).parent().addClass('active');
                                portfolio_grid.shuffle('shuffle', $(this).attr('data-group') );
                            });

                        }
                    }, 500);
                });

                // Portfolio hover effect init
                $(' #portfolio_grid > figure > a ').each( function() { $(this).hoverdir(); } );
                // Lightbox init
                $('.lightbox').magnificPopup({
                    type: 'image',
                    removalDelay: 300,

                    // Class that is added to popup wrapper and background
                    // make it unique to apply your CSS animations just to this exact popup
                    mainClass: 'mfp-fade',
                    image: {
                        // options for image content type
                        titleSrc: 'title',
                        gallery: {
                            enabled: true
                        },
                    },

                    iframe: {
                        markup: '<div class="mfp-iframe-scaler">'+
                                '<div class="mfp-close"></div>'+
                                '<iframe class="mfp-iframe" frameborder="0" allowfullscreen></iframe>'+
                                '<div class="mfp-title mfp-bottom-iframe-title"></div>'+
                              '</div>', // HTML markup of popup, `mfp-close` will be replaced by the close button

                        patterns: {
                            youtube: {
                              index: 'youtube.com/', // String that detects type of video (in this case YouTube). Simply via url.indexOf(index).

                              id: 'v=', // String that splits URL in a two parts, second part should be %id%
                              // Or null - full URL will be returned
                              // Or a function that should return %id%, for example:
                              // id: function(url) { return 'parsed id'; }

                              src: '//www.youtube.com/embed/%id%?autoplay=1' // URL that will be set as a source for iframe.
                            },
                            vimeo: {
                              index: 'vimeo.com/',
                              id: '/',
                              src: '//player.vimeo.com/video/%id%?autoplay=1'
                            },
                            gmaps: {
                              index: '//maps.google.',
                              src: '%id%&output=embed'
                            }
                        },

                        srcAction: 'iframe_src', // Templating object key. First part defines CSS selector, second attribute. "iframe_src" means: find "iframe" and set attribute "src".
                    },

                    callbacks: {
                        markupParse: function(template, values, item) {
                         values.title = item.el.attr('title');
                        }
                    },
                });
                $('.ajax-page-load-link').magnificPopup({
                    type: 'ajax',
                    removalDelay: 300,
                    mainClass: 'mfp-fade',
                    gallery: {
                        enabled: true
                    },
                });
            } // To Page portfolio
            if(to.name == 'portfoliodetail'){
                $('.pt-page').addClass('pt-page-current pt-page-moveFromTop')
            } // To Page portfoliodetail
            if(to.name == 'contact'){
                $('.pt-page').addClass('pt-page-current pt-page-flipInRight')
            } // To Page contact

            var windowWidth = $(window).width();
            if (windowWidth > 991) {

                // Custom Subpage Scroll
                $(".pt-page").mCustomScrollbar({
                    scrollInertia: 1000
                });

                // Custom Header Scroll
                $("#site_header").mCustomScrollbar({
                    scrollInertia: 8
                });
            } else {
                $(".pt-page").mCustomScrollbar('destroy');

                $("#site_header").mCustomScrollbar('destroy');
            }
            $('.tilt-effect').tilt();
        }, 100);
    });
})


