<template lang="html">
<!-- Contact Subpage -->
<section class="pt-page pt-page-5" data-id="contact">
  <div class="border-block-top-110"></div>
  <div class="section-inner">
    <div class="section-title-block">
      <div class="section-title-wrapper">
        <h2 class="section-title">{{ name }}</h2>
        <h5 class="section-description">{{ description }}</h5>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-6 col-md-6 subpage-block">
        <div class="block-title">
          <h3>{{ information_title }}</h3>
        </div>
        <p>{{ information_bio }}</p>
        <div class="contact-info-block" v-for="i in informations">
          <div class="ci-icon">
            <i :class="'pe-7s-icon '+i.icon"></i>
          </div>
          <div class="ci-text">
            <h5>{{ i.name }}</h5>
          </div>
        </div>
      </div>

      <div class="col-sm-6 col-md-6 subpage-block">
        <div class="block-title">
          <h3>Contact Form</h3>
        </div>
        <form id="contact-form" onsubmit="return false">

          <div class="messages"></div>

          <div class="controls">
            <div class="form-group">
                <input id="form_name" v-model="form_name" type="text" name="name" class="form-control" placeholder="Full Name" required="required" data-error="Name is required.">
                <div class="form-control-border"></div>
                <i class="form-control-icon pe-7s-user"></i>
                <div class="help-block with-errors"></div>
            </div>

            <div class="form-group">
                <input id="form_email" v-model="form_email" type="email" name="email" class="form-control" placeholder="Email Address" required="required" data-error="Valid email is required.">
                <div class="form-control-border"></div>
                <i class="form-control-icon pe-7s-mail"></i>
                <div class="help-block with-errors"></div>
            </div>

            <div class="form-group">
                <textarea id="form_message" v-model="form_message" name="message" class="form-control" placeholder="Message for Me" rows="4" required="required" data-error="Please, leave me a message."></textarea>
                <div class="form-control-border"></div>
                <i class="form-control-icon pe-7s-comment"></i>
                <div class="help-block with-errors"></div>
            </div>

            <button class="button btn-send" v-on:click="send">Send message</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</section>
<!-- End Contact Subpage -->
</template>

<script>
export default {
    data () {
        return {
            name: 'Contact',
            description:'Get in Touch',
            information_title:'Get in Touch',
            information_bio:'Please complete the following form and email us, we will contact you in the earliest time.',
            informations:[
                {
                    icon:'pe-7s-map-marker',
                    name:'To Hien Thanh street, Ward 13, District 10, Ho Chi Minh City'
                },{
                    icon:'pe-7s-mail',
                    name:'phuquanglxc@gmail.com'
                },{
                    icon:'pe-7s-call',
                    name:'Unvailable'
                },{
                    icon:'pe-7s-check',
                    name:'Freelance Available'
                }
            ],
            form_name:'',
            form_email:'',
            form_message:'',
        }
    },
    methods: {
        send() {
            var form = {
                name : this.form_name,
                email : this.form_email,
                message : this.form_message,
            }
            // console.log( form );
            // axios(this.endpoint + id)
            //   .then(response => {
            //     this.post = response.data
            //   })
            //   .catch( error => {
            //     console.log('-----error-------');
            //     console.log(error)
            //   })
            // this.message("Hello "+this.form_name, 'success')
            this.message('Sorry not available :(', 'info');
        },
        message(messageText, type){
            // Type is class: primary, secondary, success, danger, warning, info, light, dark
            var messageAlert = 'alert-' + type;

            var alertBox = '<div class="alert ' + messageAlert + ' alert-dismissable"><button type="button" class="close" data-dismiss="alert" aria-hidden="true">&times;</button>' + messageText + '</div>';
            if (messageText && type) {
                $('#contact-form').find('.messages').html(alertBox);
                $('#contact-form')[0].reset();
            }
        }
    },
}
</script>



